import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    grid: localStorage.grid === 'true'? true: false,
  },
  mutations: {
    changeCardsMode(state) {
      console.log('state.grid',state.grid)
      console.log('localStorage.grid',typeof(!localStorage.grid))
      state.grid = !state.grid;
      localStorage.grid = state.grid;
      console.log('localStorage.grid',localStorage.grid)
    },
  },
  actions: {
  },
  modules: {
  }
})
