<template>
  <v-container>
    <v-card class="mx-auto" max-width="800">
      <v-img class="white--text align-end" height="340px" :src="article.img">
        <v-card-title>{{article.title}}</v-card-title>
      </v-img>

      <v-card-subtitle class="pb-0">{{article.date | dateToDayMonthYear}}</v-card-subtitle>

      <v-card-text class="text--primary">
        <div>{{article.header}}</div>
      </v-card-text>

      <v-card-actions>
        <v-btn color="orange" text @click="goTo">Details</v-btn>
      </v-card-actions>
    </v-card>
  </v-container>
</template>

<script>
export default {
  name: "HelloWorld",
  props: {
    article: {
      title: null
    }
  },

  data: () => ({}),
  created() {
    console.log("in card", this.article);
  },
  methods: {
    goTo(){
      console.log('this.$router.path',this.$route.path, this.article.id )
      this.$router.push({ path: this.$route.path +'/' + this.article.id, params: { 'article': this.article.body } })
    }
  }
};
</script>
